import React from 'react';
import dynamic from 'next/dynamic';
import Head from 'next/head';

// Use dynamic import to prevent SSR errors from react-globe.gl dependency
const AdminStateExplorer = dynamic(() => import('../components/AdminStateExplorer'), {
    ssr: false,
    loading: () => <div className="h-screen w-full bg-black flex items-center justify-center text-slate-400">Loading Cartfax Atlas...</div>,
});

export default function Home() {
    return (
        <>
            <Head>
                <title>Cartfax: The Global Legal Cannabis Atlas</title>
                <meta name="description" content="A data-driven guide to legal cannabis jurisdictions, retailers, and batch results." />
            </Head>
            {/* The Explorer is the new application shell */}
            <AdminStateExplorer />
        </>
    );
}